@import url('https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap');

*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
  font-size: 20px;
}

::selection {
  background: #2ddab8;
  color: white;
}

body {
  margin: 0;
  padding: 0;
  background: #0c0f13;
  overflow-y: scroll;
  font-family: 'Sulphur Point', sans-serif;
  color: #8b8b8b;
  font-size: 0.9rem;
}

a {
  color: #545454;
  text-decoration: none;
}

a:focus, 
a:hover {
  color: #8b8b8b;
}

#root {
  /* width: 100vw;
  height: 100vh;
  overflow: hidden; */
}

.loading {
  padding: 10px;
  transform: translate3d(-50%, -50%, 0);
  color: #8b8b8b;
}

.scrollArea {
  position: relative;
  width: 100%;
}

.canvas {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.frame {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding: 1.5rem;
  z-index: 2;
}

.frame__title {
  font-size: inherit;
  font-weight: normal;
  margin: 0;
}

.frame__links {
  margin: 1rem 0;
}

.frame__link:not(:first-child) {
  margin-left: .5rem;
}

.frame__copy {
  color: #545454;
}

.frame__copy svg {
  width: .846em;
  height: 1.154em;
  fill: currentColor;
}

.frame__nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
}

.frame__nav .frame__link:first-child {
  margin-right: 1.5rem;
}

.bottom-left {
  padding: 1.5rem;
  transform: translate3d(0, -100%, 0) !important;
}

.canvas > div {
  z-index: 10000000;
}

.modal-video {
  backdrop-filter: blur(4px);
}

.modal-video-body {
  max-width: 1240px;
}

.modal-video-close-btn {
  cursor: pointer;
}



@media screen and (min-width: 53em) {
	.frame {
    display: grid;
    height: 100vh;
    padding: 1.6rem 3.2rem;
    pointer-events: none;
		align-content: space-between;
		grid-template-columns: 20% 80%;
		grid-template-rows: auto auto auto;
		grid-template-areas: 'title links'
							'... ...'
							'cp nav';
	}
	.frame__title {
    grid-area: title;
	}
	.frame__links {
		grid-area: links;
    justify-self: end;
    margin: 0;
  }
  .frame__link {
    pointer-events: auto;
  }
  .frame__copy {
    grid-area: cp;
  }
  .frame__nav {
    grid-area: nav;
		justify-self: end;
  }
  .bottom-left {
    padding: 0 0 2rem 3.7rem;
  }
  .subtitle {
    font-size: 1vw;
  }
}